<template>
    <article>

        <table data-v-regInput>
            <tr>
                <td>Login ID</td>
                <td><input type="text" name="id" placeholder=" " data-v-regInput></td>
            </tr>
            <tr>
                <td>Email</td>
                <td><input type="email" name="email" placeholder=" " data-v-regInput></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="password" name="pw" placeholder=" " data-v-regInput></td>
            </tr>
            <tr>
                <td>()</td>
                <td><input type="password" name="pw-re" placeholder=" " data-v-regInput></td>
            </tr>
        </table>
        <div class="info" data-v-regInput>
        <p>Login ID<br></p>
        <p>Email</p>
        </div>

        <div class="buttonBase" v-on:click="toRegister"></div>

    </article>
</template>

<script>
export default {
    name : "RegInput",

    methods:{
        toRegister(){
            // ToDo call Register action
            this.$router.push({name:'reg-comp'})
        }
    }
}
</script>


<style>
    table[data-v-regInput]{
        width: 100%;
        margin: 0 auto;
        /* margin-bottom: 30px; */
    }

    .info[data-v-regInput]{
        margin: 30px 0px;
        font-size: 80%;
    }
</style>



